.main-card {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.img-card img {
    border-radius: var(--border-radius-primary);
    margin-right: var(--margin-primary);
}

.list-card {
    display: flex;
    justify-content: space-between;
    padding:calc( var(--padding-primary)/4);
    margin-right: var(--margin-primary);
}
.list-card ul {
    margin-right: var(--margin-primary);
    line-height: 2;
}

.title {
    font-size: 18px;
}
.about {
    font-size: 14px;
    color: #747474;
}
