
.most-popular-item {
    flex: 1 25%;
}
.card-wrapper{
    background-color: var(--color-dark);
    padding: calc(var(--padding-primary) / 2 );
    border-radius: calc(var(--border-radius-primary) / 2 );
    margin: calc(var(--margin-primary) / 5);
}

.most-popular-image {
    width: 100%;
    border-radius: calc(var(--border-radius-primary) / 2 );
    margin-bottom: calc(var(--margin-primary) / 2 );
}

.most-popular-context {
    display: flex;
    justify-content: space-between;
}

.most-popular-context h4 {
    font-size: 14px;
    line-height: 1.5rem;
}
.most-popular-context h4 span {
    color: rgb(116, 116, 116);
}
